body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App{
  height: 100vh;
  width: 100vw;
  background-color: #f7eac6;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


/* Dropdown */

.title{
  font-size: 1.2rem;
  margin-bottom: 15px;
}
.dd-wrapper{
  margin-bottom: 150px;
  background-color: white;
  display: inline-block;
  position: relative;
  min-width: 100%;
}

.dropdownOpen{
  position: absolute;
  background-color: white;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  z-index: 1;
  padding: 0;
  margin: 0;
  width: 100%;
}

.dropdownClose{
  display: none;
}

.dd-header{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
  text-transform: capitalize;
}

.dd-header:hover{
  cursor: pointer;
}

.dd-title{
  margin-right: 10px;
}

.dd-header img{
  height: 15px;
  width: 15px;
}

li{
  list-style: none;
  padding: 10px;
  border-top: 1px grey solid;
}

li:hover{
  background-color: lightgrey;
  cursor: pointer;
}


/* Tooltip */

.tooltip {
  position: relative;
  display: inline-block;
  border: 2px solid black;
  background-color: rgb(235, 115, 115);
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  padding: 10px;
  border-radius: 15px;
  font-size: 1.5rem;
}

.tooltipText{
  font-size: 1rem;
}

.tooltip:hover{
  cursor: pointer;
  background-color: rgb(197, 72, 72);
}


.tooltip .tooltipText{
  visibility: hidden;
  width: fit-content;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 10px;
  position: absolute;
  z-index: 1;
}


/* Top tooltip */

.tooltip .tooltipTop {
  bottom: 150%;
  left: 50%;
  margin-left: -60px;
}

.tooltip .tooltipTop::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

.tooltip:hover .tooltipTop{
  visibility: visible;
}



/* Bottom tooltip */

.tooltip .tooltipBottom {
  top: 150%;
  left: 50%;
  margin-left: -60px;
}

.tooltip .tooltipBottom::after {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent black transparent;
}

.tooltip:hover .tooltipBottom {
  visibility: visible;
}

/* Left tooltip */

.tooltip .tooltipLeft {
  top: -5px;
  right: 110%;
}

.tooltip .tooltipLeft::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent transparent black;
}
.tooltip:hover .tooltipLeft {
  visibility: visible;
}


/* Right tooltip */

.tooltip .tooltipRight {
  top: -5px;
  left: 110%;
}

.tooltip .tooltipRight::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent black transparent transparent;
}
.tooltip:hover .tooltipRight {
  visibility: visible;
}